                <h3 class="default_section_heading text-center raleway navy_blue"> <span class="font_200">Drop Your</span> Feedback </h3>
                <hr class="default_divider text-center default_divider_blue " >
            </div>
          </div>

          <div class="col-md-8 offset-md-2 pt-5">
            <form onsubmit="return false" class="form_class">
              <transition name="slide-fade" >
                <div class="row" v-if="page==1">
                  <div class="mew_form padding_0 clearfix">
                    <div class="col-sm-12">
                      <input placeholder="Your Name" class="form_inputs" name="name" required="required">
                    </div>
                    <div class="col-sm-12">
                      <input placeholder="Mobile No" class="form_inputs" name="phone" required="required">
                    </div>
                    <div class="col-sm-12">
                      <input placeholder="Email" class="form_inputs"  name="email" required="required">
                    </div>
                    <div class="col-sm-12">
                      <input placeholder="Patient ID" class="form_inputs"  name="id" required="required">
                    </div>
                    <div class="col-xs-12">
                      <div class="button_div text-center">
                        <a href="#" @click.prevent="page=2" id="submit_btn" class="bg_pink button button_default_style open_sans bg_before_navy"> 
                          > Next
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              </transition>

              <transition name="slide-fade">
                <div class="row" v-if="page==2" >
                  <div class="mew_form padding_0 clearfix">
                    <div class="col-sm-12">
                      <input placeholder="Name of Doctor/ Department" class="form_inputs" name="name" required="required">
                    </div>
                    <div class="col-sm-12" style="text-align:left; font-size:20px;">
                      How satisfied were you with the courtesy of the staff that treated you/your loved one?

                      <label class="form-check-label">Very Satisfied
                        <input type="radio" checked="checked" name="radio">
                        <span class="checkmark"></span>
                      </label>
                      <label class="form-check-label">Somewhat Satisfied
                        <input type="radio" name="radio">
                        <span class="checkmark"></span>
                      </label>
                      <label class="form-check-label">Somewhat Dissatisfied
                        <input type="radio" name="radio">
                        <span class="checkmark"></span>
                      </label>
                      <label class="form-check-label">Very Dissatisfied
                        <input type="radio" name="radio">
                        <span class="checkmark"></span>
                      </label>
            
                    </div>
                    <div class="col-xs-12">
                      <div class="button_div text-center">
                        <a href="#" @click.prevent="page=3" id="submit_btn" class="bg_pink button button_default_style open_sans bg_before_navy"> 
                          > Next
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              </transition>

              <transition name="slide-fade">
                <div class="row" v-if="page==3" >
                  <div class="mew_form padding_0 clearfix">
                    <div class="col-sm-12" style="font-size:20px;">
                      How would you rate our overall service?
                      <p class="p4">&nbsp;</p>
                      <star-rating :star-size="90" :show-rating="false" active-color="#2196F3"></star-rating>
                      <p class="p4">&nbsp;</p>
                    </div>
                    <div class="col-xs-12">
                      <div class="button_div text-center">
                        <a href="#/thank" id="submit_btn" class="bg_pink button button_default_style open_sans bg_before_navy"> 
                          > Next
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              </transition>
            </form>
          </div>
        </div>
      </div>
    </div>
</section>

</template>

<script>
import StarRating from 'vue-star-rating'
export default {
  components: {
    StarRating
  },
  data() {
    return {
      page : 1
    }
  },
}
</script>

<style>
.mew_form {
  width: 100%
}
.slide-fade-enter-active {
  transition: all .5s ease;
}
.slide-fade-leave-active {
  opacity: 0;
  /*transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);*/
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateX(250px);
  opacity: 0;
}


/* The container */
.form-check-label {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default radio button */
.form-check-label input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

/* Create a custom radio button */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: rgb(150, 147, 147);
  border-radius: 50%;
}

/* On mouse-over, add a grey background color */
.form-check-label:hover input ~ .checkmark {
  background-color: rgb(161, 197, 250);
}

/* When the radio button is checked, add a blue background */
.form-check-label input:checked ~ .checkmark {
  background-color: #2196F3;
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the indicator (dot/circle) when checked */
.form-check-label input:checked ~ .checkmark:after {
  display: block;
}

/* Style the indicator (dot/circle) */
.form-check-label .checkmark:after {
 	top: 9px;
	left: 9px;
	width: 8px;
	height: 8px;
	border-radius: 50%;
	background: white;
}

.vue-star-rating{
  margin: 0 auto;
}

.contact_form_inner {
  background-color: transparent;
}

#contact-form::before{
  background: url('../images/medical_slide_2.jpg'); 
  background-size: cover;
  transform: translate3d(0px, 0px, 0px);
  height: 100vh;
  display: flex;
  justify-content: center;

  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -2;
  opacity: 0.2;

}

.mew_form .form_inputs{
  background-color: white
}
</style>
